const Signin = () => {
	const {state, dispatch} = useContext(MyContext);
	const mode = state.mode;
	// const styles = useStyles();
	console.log('signin render()', mode)

	if (mode !== SCREEN_MODE_SIGN_IN) {
		console.log("signin hidden return")
		return (
			<div className="hidden"/>
		)
	}
	console.log("signin general return")
	return (
		<Box paddingTop='10rem' textAlign='center' className={"container"}>
			<img src={logo} className="signinLogo"/>
			<h1 className={"signinTitle"}>천권읽기 로그인</h1>
			<div>
				<Button className="loginButton buttonColorGoogle"
						size={"large"}
				        color={"inherit"}
						variant={"contained"} onClick={
					()=> {
						loginGoogle(dispatch);
					}
				}>Google 로 로그인</Button>
			</div>
			<div>
				<Button className="loginButton"
				        size={"large"}
				        variant={"contained"} onClick={
					()=> {
						loginKakao(dispatch);
					}
				}>카카오로 로그인</Button>
			</div>
			<div>
				<button className={"loginButton buttonColorNaver"}
				        size={"large"}
				        variant={"contained"} onClick={
					()=> {
						loginNaver(dispatch);
					}
				}>네이버로 로그인</button>
			</div>
		</Box>
	)
}

export default Signin;